import React from "react";
import Navigation from "../components/Navigation";
import Logo from "../components/Logo";
import ContactForm from "../components/ContactForm";
import { copyToClipboard } from "copy-to-clipboard";
import SocialNetwork from "../components/SocialNetwork";
import Buttons from "../components/Buttons";
import Mouse from "../components/Mouse";
import { motion } from "framer-motion";

const Contact = () => {
  const pageTransition = {
    in: {
      opacity: 1,
      x: 0,
    },
    out: {
      opacity: 0,
      x: 200,
    },
  };
  return (
    <div>
      <Mouse />
      <motion.div
        className="contact"
        initial="out"
        animate="in"
        exit="out"
        variants={pageTransition}
        transition={{ duration: 0.4 }}
      >
        <Navigation />
        <Logo />
        <ContactForm />
        <div className="contact-infos">
          <div className="address">
            <div className="content">
              <h4>adresse</h4>
              <p>RUE L130 </p>
              <p>cocody angré</p>
            </div>
          </div>
          <div className="phone">
            <div className="content">
              <h4>telephone</h4>
              <copyToClipboard text="0151627333" className="hover">
                <p
                  style={{ cursor: "pointer" }}
                  className="clipboard"
                  onClick={() => alert("numero de telephone copié !")}
                >
                  01 51 62 08 17
                </p>
              </copyToClipboard>
            </div>
          </div>
          <div className="email">
            <div className="content">
              <h4>email</h4>
              <copyToClipboard text="ramadanesng@gmail.com" className="hover">
                <p
                  style={{ cursor: "pointer" }}
                  className="clipboard"
                  onClick={() => alert("adresse email copié !")}
                >
                  ramadanesng@gmail
                </p>
              </copyToClipboard>
            </div>
          </div>
          <SocialNetwork />
        </div>
      </motion.div>
      <Buttons left={"/projet-4"} />
    </div>
  );
};

export default Contact;
